body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-9xl: 1.75rem;

  /* Colors */
  --color-gray-100: #222;
  --color-gray-200: rgba(0, 0, 0, 0.4);

  /* Gaps */
  --gap-29xl: 3rem;

  /* Paddings */
  --padding-5xl: 1.5rem;

  /* Border radiuses */
  --br-mini: 15px;
}
